* {
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

body {
    background-color: #FFF;
    overflow: hidden;
    user-select: none;
}

.sideManu {
    width: 450px;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: calc(100vw - 450px);
    background-color: #fafafa;
    overflow: hidden;
    border-left: 1px solid rgba(0,0,0,0.1);
}

.categoryMenu {
    line-height: 50px;
    height: 50px;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    box-shadow: 0px 1px 3px rgba(0,0,0,0.05);
}

.categoryItem {
    float: left;
    text-align: center;
    width: calc(450px / 5);
    font-size: 12px;
    cursor: pointer;
    color: #777;
    font-weight: 600;
    transition: all 0.1s;
    border-right: 1px solid rgba(0,0,0,0.08);
    box-shadow: inset 0px 0px 0px #555;
}

.categoryItem:hover {
    background-color: rgba(0,0,0,0.03);
}

.categoryItemOn {
    background-color: rgba(0,0,0,0.06) !important;
    box-shadow: inset 0px 5px 0px #555;
    color: #666;
}

.itemButton {
    width: 120px;
    height: 150px;
    display: inline-block;
    overflow: hidden;
    margin: 12px 0px 0px 12px;
    cursor: pointer;
    padding: 4px;
    transition: all 0.15s;
    border-radius: 5px;
}

.itemButton:hover {
    background-color: rgba(0,0,0,0.14);
}

.itemOpen {
    background-color: #666 !important;
}

.itemOpen > .itemLabel {
    color: #FFF !important;
}

.itemImage {
    width: 120px;
    height: 120px;
    background-size: 120px auto;
    background-repeat: no-repeat;
    border-radius: 6px;
}

.itemLabel {
    line-height: 30px;
    height: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 12px;
    text-align: center;
    font-weight: bold;
    color: #888;
}

.editsTitle {
    line-height: 45px;
    height: 45px;
    padding-left: 20px;
    margin-bottom: 10px;
    margin-top: 8px;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    border-top: 1px solid rgba(0,0,0,0.1);
    box-shadow: 0px 1px 3px rgba(0,0,0,0.05);
    font-size: 13px;
    font-weight: bold;
    color: #555;
}







.editButton {
    width: 70px;
    height: 100px;
    display: inline-block;
    overflow: hidden;
    margin: 12px 0px 0px 12px;
    cursor: pointer;
    padding: 7px;
    transition: all 0.15s;
    border-radius: 5px;
}

.editButton:hover {
    background-color: rgba(0,0,0,0.14);
}

.editOpen {
    background-color: #AAA !important;
}

.editOpen > .editTitle {
    color: #222 !important;
}

.editImage {
    width: 70px;
    height: 70px;
    background-size: 120px auto;
    background-repeat: no-repeat;
    border-radius: 6px;
}

.editTitle {
    line-height: 30px;
    height: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 11px;
    text-align: center;
    font-weight: bold;
    color: #888;
}

.loader {
    position: fixed;
    width: 80px;
    height: 80px;
    z-index: 6;
    top: calc(50vh - 40px);
    left: calc(50vw - 265px);
    background-image: url(load.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

@media (orientation: portrait) {
    .sideManu {
        width: 100vw;
        height: calc(100vh - 50vw - 20px);
        position: fixed;
        top: calc(85vw);
        left: 0px;
        background-color: #fafafa;
        border-left: none;
        overflow-y: scroll;
        padding-bottom: 20px;
    }

    .itemButton {
        width: 55px;
        height: 55px;
        margin: 6px 0px 0px 6px;
        padding: 3px;
        border-radius: 3px;
    }

    .itemImage {
        width: 55px;
        height: 55px;
        background-size: 55px auto;
        border-radius: 3px;
    }

    .itemOpen {
        background-color: #06c !important;
    }

    .loader {
        background-size: 40px auto;
        left: calc(50vw - 40px);
        top: calc(45vw - 40px);
    }

    .categoryMenu {
        height: 40px;
        line-height: 40px;
    }

    .categoryItem {
        width: auto;
        padding: 0px 20px;
    }

    .editsTitle {
        line-height: 35px;
        height: 35px;
    }

    .editButton {
        width: 50px;
        height: 50px;
        padding: 3px;
    }

    .editImage {
        width: 50px;
        height: 50px;
    }
    .editOpen {
        background-color: #06c !important;
    }
    
}